footer{
    background-color: white;
    width: 100%;
    border-top-style: solid;
    border-width: 2px;
    border-color: #145f2b;
    display: flex;
    justify-content: space-around;
    color: #145f2b;
    padding-bottom: 20px;
    font-family: 'Lobster', cursive;
}
.dny{
    display:inline;
    margin-top: 0%;
}
.otvr{
    margin-left: -5%;
}
.otvr h3{
    margin-left: 20px;
}
.soc-nadpis{
    margin-left: 10%;
}
.otvr:hover{
    cursor: default;
}
.soc-nadpis:hover{
    cursor: default;
}
.kontakt:hover{
    cursor: default; 
}
.hodiny{
    padding-left: 30px;
}

.kont{
    margin-left: 70px;
}
.tabulka-kontakt{
    padding-top: 30px;
}
.logo{
    width: 80px;
}
.vw{
    width: 60px;
    margin-bottom: 3%;
    margin-right: 10px;
}
.skoda{
    width: 60px;
    margin-bottom: 3%;
    margin-left: 5px;
}
.soc-obr{
    margin-left: -25%;
    margin-top: 10%;
}
.icons{
    width: 80px;
    margin-left: 10%;
    justify-content: space-around;
    display: flex;
    padding-right: 50px;
}
.fb-ig{
    margin-left: 5%;
    width: 60%;
}
.icons img:hover{
    cursor: pointer;
}
.mobile{
    display: none;
}


@media (max-width: 700px) {

    footer{
        justify-content: center;
        display: inline-table;
    }
    .mobile i {
        margin-right: 10px;
        
        
    }
    .tabulka-kontakt{
        margin-top: -30px;
    }
    .kontakt{
       width: 100%;
       
       margin-left: 35%;
    }
    .mobile{
        display: flex;
        margin-left: 30%;
        font-weight: normal;
    }
    .mobile:hover{
        cursor: pointer;
    }
    .soc{
        margin-top: 40px;
        margin-left: 35%;
        
    }
    .otvr{
        margin-top: 30px;
        margin-left: 40%;
    }
    .soc-obr{
        display: none;
    }
    .icons{
        margin-left: 15%;
    }

}

@media (max-width: 900px) {
    .soc-obr{
        display: none;
    }
    .soc-nadpis{
        margin-left: 15%;
    }
    .icons{
        margin-left: 15%;
    }
}


@media (max-width: 480px) {
    .kontakt{
        margin-left: 15%;
    }
    .soc{
        margin-left: 25%; 
    }
    .otvr{
        margin-left: 25%;
    }
    .otvr h3{
        margin-left: 5px;
    }
}