.Nav{
    display: flex;
    justify-content: space-around;
    background-color: white;
    color: #145f2b;
    font-family: 'Lobster', cursive;
    border-bottom-style: solid;
    border-width: 1px;
    
 
}

ul{
    display: flex;
    justify-content: space-around;
    width: 40%;
    list-style: none;
    margin-top: 20px;
}

.link{
    color: #145f2b;
    text-decoration: none;
    transition: 0.4s;
    font-weight: bold;
    font-size: 19px;
}

.link:hover{
    color: black;
    transform: translateY(3px);
    
}
.nadpis:hover{
    cursor:default;
}
.icon{
    display: none;
}
.list-show{
    display: none;
}
@media (max-width: 700px) {
nav{
    width:100%;
}
    .icon{
    display: block;
    margin-top: 22px;
}
i:hover{
    cursor: pointer;
}
.list-hidden{
    display: none;
}
.ul-hide{
    display: none;
}
.ul-show{
    left: 0;
    opacity: 1;
    z-index: 1;
    align-content: center;
    padding-left: 0px;
    display: block;
    width: 100%;
    margin-top: 0px;
   
    
}
.link{
    display: block;
    justify-content: center;
    background-color: white;
    width: 100%;
    justify-content: center;
    padding-left: 40%;
    align-items: center;
    align-content: space-between;
    padding-top: 0px;
    padding-bottom: 15px;
    border-bottom-style: solid;
    border-color: #145f2b;
    border-width: 1px;
}
.link:hover{
    transform: none;
    background-color: #145f2b;
    color: white;

}
.list-show{
    display: block;
    justify-content: center;
    align-content: space-between;
    font-size: 20px;
    border-width: 5px;
}

}