.projekty{
    font-family: 'Lobster', cursive;
    padding-bottom: 15px;
    
}
.hide{
    display: none;
}
.projekty-nadpis
{
    display: flex;
    margin-left: 15%;
    background-color: white;
    width: 65%;
    border-radius: 15px;
    padding-top: 10px;
    padding-bottom: 10px;

    padding-left: 5%;
    color: #145f2b;
}
.nadpis{
   background-color: white;
   width: 70%;
    margin-left: 0%;
    border-radius: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10%;
    color: #145f2b;
}
.projekt{
    
    
    text-align: center;
    background-color: white;
    color: #145f2b;
    width: 60%;
    margin-left: 20%;
    border-radius: 15px;
    padding-bottom: 20px;
}
.vypis{
    border-bottom-style: solid;
    border-width: 2px;
    margin-bottom: 15px;
    text-align: left;
}
.auto{
    margin-left: 10%;
    margin-top: -30px;
    font-size: 18px;
    font-weight: normal;
}
.obsah{
    margin-left: 10%;
    margin-top: -10px;
    font-size: 20px;
    white-space: pre-line;
}
.img-projekty{
    max-width: 40%;
    height: 200px;
    margin-left: 10px;
    border-style: solid;
    border-radius: 10px;
    display: inline-flexbox;
    margin-top: 10px;
}
.dalsi_projekty{
    font-size: 20px;
    text-align: center;
}
