.intro{
    font-family: 'Lobster', cursive;
    width: 100%;
    height: 100%;
    display: flex;
    
}
.landing{
    background-color: #145f2b;
    height: 100%;
    padding-bottom: 30px;
}
.sup{

    height: 50%;
    width: 50%;
    float: right;
    margin-right: 0%;
    margin-top: 10%;
}
.intro h1:hover{
    cursor: default;
}
.text{
    background-color: white;
    float: left;
    width: 40%;
    margin-top: 8%;
    margin-left: 2%;
    padding-left: 5%;
    padding-top: 2%;
    padding-bottom: 2%;
    color: #145f2b;
    border-radius: 10px;
    
}
h1{
    text-decoration: underline;
}
.odraz:hover{
    cursor:pointer;
}
.zjisti{
background-color: #145f2b;
  border: solid;
  border-radius: 15px;
  border-width: 2px;
  color: white;
  padding: 20px 45px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  margin: 4px 2px;
  cursor: pointer;
  margin-left: 25%;
  margin-top: 40px;
  font-family: 'Lobster', cursive;
  font-weight: normal;
}
.zjisti:hover{
    background-color: white;
    color: #145f2b;
}
@media (max-width: 900px) {
    .sup{
        display: none;
        
    }
    .intro{
        text-align: center;
        width: 100%;
        margin-left: 0px;
    }
    .text{
        width: 100%;
    }
    .zjisti{
        margin-left: 0%;
    }
    .text{
        padding-left: 0px;
    }
    .intro{
        margin-left: -3px;
        width: 100%;
    }
}


