.sluzbycky{
    display: inline-flexbox;
    justify-content: space-around;
    width: 100%;
    padding-bottom: 50px;
    font-family: 'Lobster', cursive;
    color: #145f2b;
}
.sluzbycky h2{
    margin-left: 4%;
    width: 88%;
    background-color: white;
    border-radius: 15px;
    padding-bottom: 15px;
    padding-top: 15px;
    padding-left: 25px;
}
.sluzbycky ul{
    justify-content: space-around;
    margin-left: 25%;
    text-align: center;

}
.sluzbycky li{
    background-color: white;
    margin-left: 5%;
    padding-left: 60px;
    padding-right: 60px;
    padding-top: 10%;
    padding-bottom: 10%;
    border-radius: 15px;
    text-align: center;
    font-size: 19px;
    color: black;
    justify-content: center;
}
.sluzbycky li:hover{
    cursor: default;
}
.sluzby_ico{
    width: 100px;
}
.mobile-sluzby{
    display: none;
}
.sluzby-tri{
    display: none;
}
.sluzby-dve{
    display: none;
}
.sluzby-jedna{
    display: none;
}
@media (max-width: 1250px) {
    
    .Sluzby-ul{
        display: none;
    }
    .sluzby-tri{
        display: flex;
        margin-left: 50%;
    }
    .sluzby-dve{
        display: none;
    }
    .sluzby-jedna{
        display: none;
    }
}  
@media (max-width: 1000px) {
    
    .Sluzby-ul{
        display: none;
    }
    .sluzby-tri{
        display: none;
        margin-left: 50%;
    }
    .sluzby-dve{
        display: flex;
    }
    .sluzby-jedna{
        display: none;
    }
}  
@media (max-width: 450px) {
    
    .Sluzby-ul{
        display: none;
    }
    .sluzby-tri{
        display: none;
        margin-left: 50%;
    }
    .sluzby-dve{
        display: none;
    }
    .sluzby-jedna{
        display: inline;
        justify-content: space-around;
    }
  
}
@media (max-width: 520px) {
    .Sluzby-ul{
        display: none;

    }
    .mobile-sluzby{
        display: inline;
        margin-left: 0%;
        justify-content: space-around;
        margin-top: 0px;
    }
    .mobile-sluzby li{
        margin-left: 3%;
        width: 70%;
        margin-top: 20px;
    }
    .sluzbycky h2{
        text-align: center;
        margin-left: 0px;
        width: 100%;
        border-radius: 0;
        padding-left: 0px;
        margin-bottom: 0;
    }
    .sluzby-tri{
        display: none;
    }
}
@media (max-width: 480px) {
    .Sluzby-ul{
        display: none;

    }
    .mobile-sluzby{
        display: inline;
        margin-left: 0%;
        justify-content: space-around;
        margin-top: 0px;
    }
    .mobile-sluzby li{
        margin-left: 3%;
        width: 50%;
        margin-top: 20px;
    }
    .sluzbycky h2{
        text-align: center;
        margin-left: 0px;
        width: 100%;
        border-radius: 0;
        padding-left: 0px;
        margin-bottom: 0;
    }
    .sluzby-tri{
        display: none;
    }
}