.kontakt-page{
    color: #145f2b;
    background-color: white;
    margin-top: 0%;
    width: 80%;
    margin-left: 10%;
    font-family: 'Lobster', cursive;
    padding-bottom: 20px;
    margin-bottom: 50px;
    margin-top: 50px;
    border-radius: 15px;
}
.kontakt-page h1{
    margin-left: 10%;
    padding-top: 5%;
    padding-bottom: 50px;
}
.udaje{
    display: flex;
    margin-left: 12%;
}
.contact-form{
    justify-content: center;
    background-color: white;
    width: 60%;
    margin-left: 20%;
    border-style: solid;
    border-width: 2px;
    border-color: #145f2b;
    border-radius: 10px;
    padding-bottom: 10px;
    margin-bottom: 20px;
    margin-top: 5%;

}

.inputs{
    display: flex;
    margin-left: 40%;
    border-radius: 5px;
    border-color: #145f2b;
    box-sizing: border-box;
    border-style: solid;
    padding-bottom: 5px;
    padding-top: 5px;
    
}
.tabulka-cont{
    font-size: 18px;
}
.inputs:focus{
    border-color: white;
}
.contact-form label{
    margin-left: 40%;
    font-size: 20px;
}
.zprava{
    margin-left: 25%;
    resize: none;
    border-radius: 5px;
    border-color: #145f2b;
    box-sizing: border-box;
    border-style: solid;
    border-width: 2px;
}

.contact-form h2{
 margin-left: 40%;
}
.poslat{
    background-color: #145f2b;
  border: solid;
  border-radius: 15px;
  border-width: 2px;
  color: white;
  padding: 20px 45px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  margin: 4px 2px;
  cursor: pointer;
  margin-left: 43%;
  margin-top: 40px;
  font-family: 'Lobster', cursive;
  font-weight: normal;
}
.mapa{
    width: 80%;
    height: 100%;
    border-style: solid;
    border-width: 2px;
    border-radius: 15px;
    
}
.map{
    position:relative;
    text-align: center;
    height:382px;
    width:80%;
    margin-left: 2%;
    margin-top: -10%;
    
}
.map-mobile{
    display: none;
}
@media (max-width: 700px) {
    .contact-form{
        justify-content: center;
        background-color: white;
        width: 100%;
        margin-left: 0px;
        border-style: none;
        border-width: 2px;
        border-color: #145f2b;
        border-radius: 0px;
        padding-bottom: 10px;
        margin-bottom: 20px;
        margin-top: 5%;
    
    }
    .inputs{
        display: flex;
        margin-left: 27%;
        border-radius: 5px;
        border-color: #145f2b;
        box-sizing: border-box;
        border-style: solid;
        padding-bottom: 10px;
        padding-top: 10px;
        font-size: 20px;
        
    }
    .contact-form label{
        margin-left: 30%;
        font-size: 20px;
    }
    .kontakt-page{
        margin-top: 0%;
        width: 100%;
        margin-left: 0%;
        padding-bottom: 20px;
        margin-bottom: 50px;
        margin-top: 50px;
        border-radius: 0px;
        
    }
  
    iframe{
        width: 400px;
    }
    .poslat{
        margin-left: 40%;
    }
    .zprava{
        margin-left: 20%;
        
    }

}

@media (max-width: 1150px) {

    .contact-form{
        justify-content: center;
        background-color: white;
        width: 100%;
        margin-left: 0px;
        border-style: none;
        border-width: 2px;
        border-color: #145f2b;
        border-radius: 0px;
        padding-bottom: 10px;
        margin-bottom: 20px;
        margin-top: 5%;
    
    }
    .inputs{
        display: flex;
        margin-left: 27%;
        border-radius: 5px;
        border-color: #145f2b;
        box-sizing: border-box;
        border-style: solid;
        padding-bottom: 10px;
        padding-top: 10px;
        font-size: 20px;
        
    }
    .contact-form label{
        margin-left: 30%;
        font-size: 20px;
    }
}


@media (max-width: 480px) {
    .tabulka-cont{
        margin-left: -20px;
        font-size: 17px;
    }
    .hodiny{
        padding-left: 0px;
    }
    .tabulka-cont td{
        margin-right: 0px;
    }
    .map{
        display: none;
    }
    .map-mobile{
        display: inline;
        margin-top: 20%;
    }
    .mapa{
        width: 100%;
        height: 150%;
        border-style: solid;
        border-width: 2px;
        border-radius: 15px;
        margin-top: 5%;
    }
    .contact-form input{
        margin-left: 0px;
        width: 100%;
    }
    .zprava{
        margin-left: 0px;
        width: 100%;
    }
    .contact-form label{
        margin-left: 0px;
    }
    .contact-form h2{
        margin-left: 0px;
    }
}
